.page {
  width: 267mm;
  display: flex;
  min-height: 180mm;
  break-after: page;
}

/* When the document is actually printed */
@media print {
	.page {
		/* Force page breaks after each .page element of the document */
    page-break-after: always;
    break-after: page;
    width: 267mm;
    /* Reflect the paper height in the screen rendering (must match size from @page rule) */
    min-height: 180mm;
	}
}

.Zettel {
  width: 89mm;
  height: 180mm;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: .5mm dotted black;
}

.Zettel:last-child {
  border-right: none;
}

.Kopfzeile {
  width: 89mm;
  height: 32.5mm;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  border-bottom: .5mm solid black;
  flex-direction: column;
}

.KopfzeileKopf {
  width: 89mm;
  display: flex;
  justify-content: space-between;
}

.KopfzeileKopf h3 {
  margin-left: 5mm;
  margin-right: 5mm;
}

.KopfzeilenText {
  margin-left: 5mm;
}

.Tabelle {
  flex: 1 1 95mm;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}

.Tabelle .Zeile {
  flex: 0 0 89mm;
  display: flex;
  border-bottom: .5mm solid black;
}

.Tabelle .Zeile:last-child {
  border-bottom: none;
}

.Tabelle .Zeile .SpalteA {
  flex: 0 0 10%;
}

.Tabelle .Zeile .SpalteB {
  flex: 0 0 70%;
  border-left: .5mm solid black;
  border-right: .5mm solid black;
}

.Tabelle .Zeile .SpalteB span {
  margin-left: 5mm;
  line-height: 5mm;
  font-weight: bold;
  font-size: smaller;
}

.Tabelle .Zeile .SpalteC {
  flex: 0 0 20%;
}

.Schnipsel {
  width: 89mm;
  height: 52.5mm;
  display: flex;
  border-top: .5mm dotted black;
}

.SchnipselBody {
  flex: 1 1 100%;
  padding: 3mm;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.SchnipselBodyHeadlines {
  display: flex;
  justify-content: space-between;
}

.SchnipselBodyHeadlinesNummer {
  color: red;
}

.SchnipselBodyHeadlines > h4 {
  margin: 2mm 0;
}

.SchnipselBodyTop {
  display: flex;
  justify-content: space-between;
}

.SchnipselBodyTopLeft {
  flex: 0 0 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}

.SchnipselBodyTopRight {
  flex: 0 1 65%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-end;
}

.SchnipselBodyTopRight > hr {
  width: 100%;
  margin-bottom: 0;
  margin-top: 4mm;
}

.SchnipselBodyBottom {
  font-size: small;
}

.SchnipselBodyBottomContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}


/* 210 x 297 */